import * as React from 'react';
import { Switch, Route } from 'react-router-dom';
import Home from './Home';
import Clothing from './Clothing';
import Accessories from './Accessories';
import Intimates from './Intimates';
import { Product } from '../models/Product';

function windowsScrollToTop() {
  window.scrollTo(0, 0);
}

const Listings = (products: Product[]) => (

  <main>
    {windowsScrollToTop()}
    <Switch>
      <Route exact path="/" render={() => <Home />} />
      <Route path="/clothing" render={() => <Clothing {...products}/>} />
      <Route path="/accessories" render={() => <Accessories {...products}/>} />
      <Route path='/intimates' render={() => <Intimates {...products}/>} />
    </Switch>
  </main>
);

export default Listings; 



