import * as React from 'react';
import { Link } from 'react-router-dom';
import * as $ from 'jquery';

function hideNavBar() {
    $('.navbar-collapse').collapse('hide') // Every time we render the navbar make sure it's collapsed
}

class Navbar extends React.Component {

    render() {
        return (
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <Link className="navbar-brand" to="/">Bela</Link>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item">
                            <Link className="nav-link" to="/clothing">Clothing</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/accessories">Accessories</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/intimates">Intimates</Link>
                        </li>
                    </ul>
                </div>
                {hideNavBar()}
            </nav>
        );
    }
}

export default Navbar;



