import * as React from 'react';
import { Product } from '../models/Product';
// import * as $ from 'jquery';

const ProductCarousel = (products: Product[]) => (
    <div id={"myCarousel" + Object.entries(products)[0][1].category} className='carousel slide' data-ride='carousel' data-wrap='false' data-interval='false'>
        <div className="carousel-inner row w-100 mx-auto">
            {Object.entries(products).map((product, count = 0) => {
                return (
                    <div key={"carousel-item"+count} className={count++ == 0 ? "carousel-item col-md-4 active" : "carousel-item col-md-4"}>
                        <div className="card">
                            <img className="card-img-top img-fluid" src={product[1].images} alt="Card image cap" />
                            <div className="card-body">
                                <h4 className="card-title">{product[1].title}</h4>
                                <p className="card-text">{product[1].price}</p>
                            </div>
                        </div>
                    </div>
                )
            })}
            <a className="carousel-control-prev" href={"#myCarousel" + Object.entries(products)[0][1].category} role="button" data-slide="prev">
                <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                <span className="sr-only">Previous</span>
            </a>
            <a className="carousel-control-next" href={"#myCarousel" + Object.entries(products)[0][1].category} role="button" data-slide="next">
                <span className="carousel-control-next-icon" aria-hidden="true"></span>
                <span className="sr-only">Next</span>
            </a>
        </div>
    </div>
);

export default ProductCarousel;



