import * as React from 'react';
import { Link } from 'react-router-dom';
import { Product } from '../models/Product';
import ProductCarousel from './Product_Carousel';
import '../css/product.css';

let products: Product[] = require('../featured_products.json');

const Home = () => (
    <div>
        <br />
        <h1 className="text-center">Bela</h1>
        <p className="text-center home-subheading">On-trend and replica designer fashion from Aliexpress</p>
        <div className="container">
            <hr />
            <div className="home-tile">
                <div className="text-center overflow-hidden header-home align-items-center">
                    <div className="my-3 py-3 header-home-img-container">
                        <img src={require('../img/home-clothing.jpeg')} />
                    </div>
                </div>
            </div>
            <br />
            <h3 className="text-center small-header">Clothing</h3>
            <br />
            <div className="row justify-content-center">
                <ProductCarousel {...products["clothing"]} />
                <Link className="btn btn-outline-dark" role="button" aria-pressed="true" to="/clothing">See More</Link>
            </div>
            <h3 className="text-center small-header">Accessories</h3>
            <br />            
            <div className="row justify-content-center">
                <ProductCarousel {...products["accessories"]} />
                <Link className="btn btn-outline-dark" to="/accessories">See More</Link>
            </div>
            <h3 className="text-center small-header">Intimates</h3>
            <br />            
            <div className="row justify-content-center">
                <ProductCarousel {...products["intimates"]} />
                <Link className="btn btn-outline-dark" to="/intimates">See More</Link>
            </div>
        </div>
    </div>
);

export default Home;



